@import url(https://fonts.googleapis.com/css2?family=Ubuntu:ital@1&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Ubuntu', sans-serif;
  /* background-color: #000; */
}
::-webkit-scrollbar {
    width: 10px;
    border-radius: 20px;
  }
  
  /* Track / */
  ::-webkit-scrollbar-track {
    background: #fff;
  }
  
  /* / Handle / */
  ::-webkit-scrollbar-thumb {
    border-radius: 50px;
    background: rgb(255, 108, 41);
  }
  
  /* / Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: #555;
  }
.gallery {
    --webkit-column-count: 3;
    --moz-column-count: 3;
    -webkit-column-count: 3;
            column-count: 3;
    --webkit-column-width: 33%;
    --moz-column-width: 33%;
    -webkit-column-count: 33%;
            column-count: 33%;
    padding: 0 12px;
}

.gallery .pics {
    transition: all 350ms ease;
    cursor: pointer;
    margin-bottom: 12px;
}

.gallery .pics:hover {
    -webkit-filter: opacity(.8);
            filter: opacity(.8);
}

@media (max-width: 991px) {
    .gallery {
        --webkit-column-count: 2;
        --moz-column-count: 2;
        -webkit-column-count: 2;
                column-count: 2;
    }
}

@media (max-width: 480px) {
    .gallery {
        --webkit-column-count: 1;
        --moz-column-count: 1;
        -webkit-column-count: 1;
                column-count: 1;
        --webkit-column-width: 100%;
        --moz-column-width: 100%;
        -webkit-column-count: 100%;
                column-count: 100%;
    }
}

.model {
    width: 100%;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    background-color: #191515;
    transition: opacity .4s ease, visibility .4s ease, trasform .4s ease-in-out;
    visibility: hidden;
    opacity: 0;
    -webkit-transform: scale(0);
            transform: scale(0);
    overflow: hidden;
    z-index: 999;

}

.model.open {
    visibility: visible;
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
}

.model img {
    width: auto;
    max-width: 100%;
    max-height: 100%;
    height: auto;
    display: block;
    line-height: 0;
    box-sizing: border-box;
    padding: 20px 0 20px;
    margin: 0 auto;
}

.model.open svg {
    position: fixed;
    top: 10px;
    right: 10px;
    width: 2rem;
    height: 2rem;
    padding: 5px;
    background-color: rgba(0, 0, 0, 0.4);
    color: #ffffff;
    cursor: pointer;
}
